import React from 'react';
import './FaqCompo.css'
const FaqCompo = props =>(
    <div className='faqCompo'>
       <h1>FAQ</h1>
       <p className='faqItem'>Questions mostly are from google and from my own expirience what people ask me most often</p>
       <div className='faqAll'>
       
       <div className="faqCompoLine">
        <h2>Can Windwalker use two-hand weapon?</h2>
         <p>Yes, he can but if u use two-hand like polearm u cannot use {props.FOF} so i do not recomend that</p>
         <h2>Which race for Windwalker monk?</h2>
         <p>pick whatever race you prefer. It is not so important but if u are hardcore player u can chose Blood Elf becouse he has {props.ACT} or maybe mechagnome or nightelf shadowmeld is usefull on M+ </p>
         </div>
         <div className="faqCompoLine">
          <h2>What are the advantages of playing Windwalker?</h2>
          <p>
            <ul>
              <li>Awesome mobility</li>
              <li>Good Aoe dps</li>
              <li>Pretty nice burst dmg</li>
              <li>Good CC</li>
            </ul>
          </p>
          <h2>What are the disadvantages of playing Windwalker?</h2>
          <p>
           <ul>
              <li>Not so good defensives</li>
              <li>Weak single target dps</li>
            </ul>
          </p>
          </div>
          </div>
    </div>
  );


export default FaqCompo;
