import React from 'react';
import './Footer.css';


function Footer(props)
 {
  return (
    <footer className="footerItems">
        
        <a className="footerLink" href={props.link} target="_blank" rel="noopener noreferrer">
            <img className="footerImage" alt={props.description} src={props.image}></img>
        </a>
       
        
        
    </footer>
  );
}

export default Footer;
