import React from 'react';
import PropTypes from 'prop-types'
import './MainList.css'
function TopicList(props)
{
    return(
    
        <div className="topicList">
            <img src={props.image} className="imgTopic" alt={props.topic}></img>            
            <h1 className="h1Topic">{props.topic}</h1>
            <p className="pTopic">{props.description}</p>
            <a href={props.link} className="buttonTopic">{props.button}</a>
        
     </div>

    );

}

export default TopicList;

TopicList.propTypes =
{
    image: PropTypes.string.isRequired,
    topic: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired
}