import React, { Component } from 'react';
import Header from '../components/Header/Header.js';
import SideDrawer from '../components/SideDrawer/SideDrawer.js';
import Backdrop from '../components/Backdrop/Backdrop.js';
import { Links } from '../Topics.js'
import { Bosses } from '../Topics.js'
import '../components/MainList/MainList.css'
import '../components/Footer/Footer.css'
import '../components/RaidTipsCompo/RaidTipsCompo.css'


import Footer from '../components/Footer/Footer.js'
import RaidTipsCompo from '../components/RaidTipsCompo/RaidTipsCompo.js'
       
class About extends Component{
  state=
  {
    sideDrawerOpen: false
  };

  drawerToggleClickHandler =()=>{
    this.setState((prevState)=>{
      return{sideDrawerOpen: !prevState.sideDrawerOpen};
    });
  };
  backdropClickHandler =()=>
    {
      this.setState({sideDrawerOpen:false});
    };
  render(){
    
    let backdrop;

    if(this.state.sideDrawerOpen)
    {
     
      backdrop = <Backdrop click={this.backdropClickHandler}/>
    }
  return (
    <div style={{height: '100%'}}>
      <Header drawerClickHandler={this.drawerToggleClickHandler} />
      <SideDrawer show={this.state.sideDrawerOpen}/>
      {backdrop}
      <div className='raidTips'>
      <h1>Raid Tips</h1>
       <hr className="hOneHr"></hr>
        
      {Bosses.map(item =>(
            <RaidTipsCompo key={item.name}{...item}  />
            ))}
     </div>
      
      <div className="footer" >
        {Links.map(item =>(
          <Footer key={item.description}{...item}/>
          
        ))}
        
        </div>
        
    </div>
    
  );
  }
}

export default About;
