import React from 'react';
import './TalentsCompo.css'
const TalentsCompo = props =>(
    <div className='talentsCompo'>
       <h1>Talents</h1>
       <hr className="hOneHr"></hr>
       <div className='talentsCompo-all'>
       <div className='talentsCompo-item'>
           <h2>PVE</h2>
                <p><span>Tier 1:</span>For single target personal preference but i prefere {props.EoTT}and for more then one target {props.CB}</p>
                <p><span>Tier 2:</span>Personal preference but i prefere {props.TL}sometimes better have more speed then use roll</p>
                <p><span>Tier 3:</span>If u have {props.PP}trait then use {props.ASC}otherwise use {props.FotWT}</p>
                <p><span>Tier 4:</span>{props.GK}or {props.ROP}if for example tank need help in kiting</p>
                <p><span>Tier 5:</span>For magic damage {props.DM}for physical {props.DH}</p>
                <p><span>Tier 6:</span>Most content {props.HC}</p>
                <p><span>Tier 7:</span>Always {props.WDP}</p>

       </div>
       <div className='talentsCompo-item'>
         <h2>PVP</h2>
                <p><span>Tier 1:</span>{props.EoTT}</p>
                <p><span>Tier 2:</span>{props.TL}</p>
                <p><span>Tier 3:</span>{props.ASC}</p>
                <p><span>Tier 4:</span>{props.ROP}</p>
                <p><span>Tier 5:</span>{props.DM}</p>
                <p><span>Tier 6:</span>{props.HC}</p>
                <p><span>Tier 7:</span>{props.WDP}</p>
                <hr></hr>
                <p><span>PVP 1:</span>{props.GM}</p>
                <p><span>PVP 2:</span>{props.RH}</p>
                <p><span>PVP 3:</span>{props.TF}</p>
                <p><span>PVP 4:</span>{props.AT}</p>
                <hr></hr>
                <p><span>BONUS:</span>{props.TODPP}good for open world content</p>
       </div>
       </div>

    </div>
  );


export default TalentsCompo;
