import React from 'react';
import './AbilityCompo.css'
const AbilityCompo = props =>(
    <div className='abilityCompo'>
       
        <h1>Ability Priority</h1>
        <p className='hOneP'>Windwalker has rotation we call Ability Priority. This means each ability has a streangth. Use Whatever ability is availiable that's the strongest one in the priority and try to not use weak ability if u have stronger availiable</p>
      <div className='abilityCompo-all'>
        <div className='abilityCompo-item'>
          <h2>Single Target</h2>
          <p><span>1:</span>{props.TOD}</p>
          <p><span>2:</span>{props.FotWT} (if talented and if less then 3 Chi)</p>
          <p><span>3:</span>{props.WDP}</p>
          <p><span>4:</span>{props.FOF}</p>
          <p><span>5:</span>{props.RSK}</p>
          <p><span>6:</span>{props.SCK} (with {props.DoC} proc)</p>
          <p><span>7:</span>{props.CB}</p>
          <p><span>8:</span>{props.CW}(if talented)</p>
          <p><span>9:</span>{props.BK}</p>
          <p><span>10:</span>{props.TP}</p>
          <p className='abilityCompo-tip'>If u have everything on CD then use {props.BK} as much as u can to reduce cd on {props.FOF}but try to not break {props.HC} </p>
          </div>
          <div className='abilityCompo-item'>
          <h2>Multi Target</h2>
          <p><span>1:</span>{props.TOD}</p>
          <p><span>2:</span>{props.WDP}</p>
          <p><span>3:</span>{props.FOF}</p>
          <p><span>4:</span>{props.RJW} (if talented)</p>
          <p><span>5:</span>{props.RSK} (only to use {props.WDP})</p>
          <p><span>6:</span>{props.SCK} (Minimum 3 stacks)</p>
          <p><span>7:</span>{props.CB} (if less then max Chi)</p>
          <p><span>8:</span>{props.BK} (to build {props.SCK}stacks)</p>
          <p><span>9:</span>{props.TP}</p>
          <p className='abilityCompo-tip'>{props.SEF}can stack your {props.SCK}and try to stay that your {props.FOF}can hit all target</p>



          </div>
         
        </div>
        <div className='abilityCompo-all'>
        <div className='abilityCompo-item'>
          <h2>Opener</h2>
          
          <p><span>1:</span>{props.XUEN}(if talented)</p>
          <p><span>2:</span>If {props.ASC}talented</p>
          <ul>
            <li><p><span>1:</span>{props.TP}</p></li>
            <li><p><span>2:</span>{props.FSK}/ {props.CB}/ {props.CW}</p></li>
            <li><p><span>3:</span>{props.TP}</p></li>
            <li><p><span>4:</span>{props.SEF}</p></li>
            <li><p><span>5:</span>{props.TOD}</p></li>
            <li><p><span>6:</span>{props.CycB}(if have)</p></li>
            <li><p><span>7:</span>{props.BLOOD}</p></li>
            <li><p><span>8:</span>{props.RSK}</p></li>
            <li><p><span>9:</span>{props.TP}</p></li>
            <li><p><span>10:</span>{props.FOF}</p></li>
            <li><p><span>11:</span>{props.WDP}</p></li>
          </ul>
          <p><span>3:</span>If {props.FotWT}talented</p>
          <ul>
            <li><p><span>1:</span>{props.TP}</p></li>
            <li><p><span>2:</span>{props.FotWT}</p></li>
            <li><p><span>4:</span>{props.SEF}</p></li>
            <li><p><span>5:</span>{props.TOD}</p></li>
            <li><p><span>6:</span>{props.CycB}(if have)</p></li>
            <li><p><span>7:</span>{props.BLOOD}</p></li>
            <li><p><span>8:</span>{props.RSK}</p></li>
            <li><p><span>9:</span>{props.FOF}</p></li>
            <li><p><span>10:</span>{props.WDP}</p></li>
          </ul>
          </div>
          <div className='abilityCompo-item'>
          <h2>Defensives</h2>
          <p><span>1:</span>{props.TOK} it is defensive but better use it offensive. If u can use it before {props.TOD}</p>
          <p><span>2:</span>{props.DM} to reduce magic damage</p>
          <p><span>3:</span>{props.DH} to reduce physical damage</p>

          <h2>Overall Tip</h2>
          <p>The most important thing is that you should never break mastery and if talented {props.HC} If u have max Chi do not use spells which generate it. You cannot lose Chi like that and try in fight time have always less then 100% energy</p>


          </div>
         
        </div>
        
    </div>
  );


export default AbilityCompo;
