import React from 'react';
import './RaidTipsCompo.css'
const RaidTipsCompo = props =>(
    <div className='raidTipsCompo'>      
       <div className='raidTipsCompo-item'><img src={props.icon} alt='icon'></img><h2>{props.name}</h2></div>
        <p>{props.tip}</p>
    </div>
  );


export default RaidTipsCompo;
