import React, { Component } from 'react';
import {HashRouter as Router, Route, Switch, Redirect} from 'react-router-dom';
//Pages
import About from "./Pages/About.js";
import NotFoundPage from "./Pages/404.js";
import MainPage from "./Pages/MainPage.js";
import FaqPage from "./Pages/FaqPage.js";
import TalentsPage from "./Pages/Talents.js";
import AbilityPage from "./Pages/Ability.js"
import BisPage from "./Pages/Bis.js"
import RaidTipsPage from "./Pages/RaidTips.js"
       
class App extends Component{
  render(){
  return (
     <Router>
          <Switch>
            <Route exact path="/" component={MainPage}/>   
            <Route exact path="/about" component={About}/>
            <Route exact path="/faq" component={FaqPage}/>
            <Route exact path="/talents" component={TalentsPage}/>
            <Route exact path="/ability-priority" component={AbilityPage}/>
            <Route exact path="/Best-in-Slot" component={BisPage}/>
            <Route exact path="/Raid-Tips" component={RaidTipsPage}/>
            <Route exact path="/404" component={NotFoundPage}/>
            <Redirect to="/404"/>
          </Switch>
        </Router>
    
  );
  }
}

export default App;
